___CSS_LOADER_EXPORT___.push([module.id, `.header-menu-container {
    display: none;
}

.header-link {
    display: flex;
    font-family: var(--headline-font-Family), sans-serif;
    font-size: 1.5rem;
    line-height: 2rem;
    letter-spacing: 1.5px;
    color: var(--surface-secondary-text);
    padding: var(--spacing-40) var(--spacing-60);
    gap: 1rem;
}

.header-link:hover {
    cursor: pointer;
}

.header-link.active {
    background-color: var(--surface-primary-background);
}

.header-link.active > .three-dots-vertical, .header-link:hover > .three-dots-vertical{
    opacity: 1;
    gap: 5px;
    -webkit-transition: 300ms ease-in;
    -moz-transition: 300ms ease-in;
    -o-transition: 300ms ease-in;
    transition: 300ms ease-in;
}

.header-link.active > .three-dots-vertical > .dot-1, .header-link:hover > .three-dots-vertical > .dot-1 {
    top: 0px;
}

.header-link.active > .three-dots-vertical > .dot-3, .header-link:hover > .three-dots-vertical > .dot-3 {
    top: 0px;
}

@media screen and (max-width: 1200px) {
    .logo {
        padding: var(--spacing-20)
    }

    .header-menu-container {
        display: flex;
        padding: var(--spacing-10) var(--spacing-20);
    }

    .header-link-container {
        display: none;
    }

    .header-link {
        padding: var(--spacing-30) var(--spacing-60) var(--spacing-30) var(--spacing-30);
        font-size: 1.5rem;
        line-height: 2rem;
        align-items: center;
    }

    .header-link.active > .three-dots-vertical, .header-link:hover > .three-dots-vertical{
        gap: 2px;
    }

    .header-mobile-menu-container{
        display: flex;
        justify-content: end;
        width: 100%;
        top: 0px;
        right: 0px;
        margin: var(--spacing-80) 0 0 0;
        background-color: RGB(0,0,0,.5);
        flex-direction: row;
        position: absolute;
        z-index: 1000;
        height: calc(100% - 80px);
    }

    .header-mobile-menu {
        height: 100%;
        background-color: var(--surface-primary-background);
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/header.css"],"names":[],"mappings":"AAAA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,oDAAoD;IACpD,iBAAiB;IACjB,iBAAiB;IACjB,qBAAqB;IACrB,oCAAoC;IACpC,4CAA4C;IAC5C,SAAS;AACb;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,mDAAmD;AACvD;;AAEA;IACI,UAAU;IACV,QAAQ;IACR,iCAAiC;IACjC,8BAA8B;IAC9B,4BAA4B;IAC5B,yBAAyB;AAC7B;;AAEA;IACI,QAAQ;AACZ;;AAEA;IACI,QAAQ;AACZ;;AAEA;IACI;QACI;IACJ;;IAEA;QACI,aAAa;QACb,4CAA4C;IAChD;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,gFAAgF;QAChF,iBAAiB;QACjB,iBAAiB;QACjB,mBAAmB;IACvB;;IAEA;QACI,QAAQ;IACZ;;IAEA;QACI,aAAa;QACb,oBAAoB;QACpB,WAAW;QACX,QAAQ;QACR,UAAU;QACV,+BAA+B;QAC/B,+BAA+B;QAC/B,mBAAmB;QACnB,kBAAkB;QAClB,aAAa;QACb,yBAAyB;IAC7B;;IAEA;QACI,YAAY;QACZ,mDAAmD;IACvD;AACJ","sourcesContent":[".header-menu-container {\n    display: none;\n}\n\n.header-link {\n    display: flex;\n    font-family: var(--headline-font-Family), sans-serif;\n    font-size: 1.5rem;\n    line-height: 2rem;\n    letter-spacing: 1.5px;\n    color: var(--surface-secondary-text);\n    padding: var(--spacing-40) var(--spacing-60);\n    gap: 1rem;\n}\n\n.header-link:hover {\n    cursor: pointer;\n}\n\n.header-link.active {\n    background-color: var(--surface-primary-background);\n}\n\n.header-link.active > .three-dots-vertical, .header-link:hover > .three-dots-vertical{\n    opacity: 1;\n    gap: 5px;\n    -webkit-transition: 300ms ease-in;\n    -moz-transition: 300ms ease-in;\n    -o-transition: 300ms ease-in;\n    transition: 300ms ease-in;\n}\n\n.header-link.active > .three-dots-vertical > .dot-1, .header-link:hover > .three-dots-vertical > .dot-1 {\n    top: 0px;\n}\n\n.header-link.active > .three-dots-vertical > .dot-3, .header-link:hover > .three-dots-vertical > .dot-3 {\n    top: 0px;\n}\n\n@media screen and (max-width: 1200px) {\n    .logo {\n        padding: var(--spacing-20)\n    }\n\n    .header-menu-container {\n        display: flex;\n        padding: var(--spacing-10) var(--spacing-20);\n    }\n\n    .header-link-container {\n       