___CSS_LOADER_EXPORT___.push([module.id, `.project-card {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    top: 0px;
    position: relative;
    width: calc((100% - var(--gutter))/2);
    background-color: var(--surface-primary-background);
    border-radius: var(--card-border-radius);
    box-shadow: var(--card-shadow);
    overflow: hidden;
    -webkit-transition: all .1s ease-out;
    -moz-transition: all .1s ease-out;
    -o-transition: all .1s ease-out;
    transition: all .1s ease-out;
}

.project-card:hover {
    top: -5px;
    position: relative;
    box-shadow: var(--card-shadow-hover);
    -webkit-transition: all .1s ease-out;
    -moz-transition: all .1s ease-out;
    -o-transition: all .1s ease-out;
    transition: all .1s ease-out;
    cursor: pointer;
}

.project-card > .image-wrapper{
    /*delete later*/
    background-color: #ddd;
    height: 300px;
}

.project-card-body {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-20);
    padding: var(--card-padding);
    border: solid 1px var(--border-divider);
    border-radius: 0 0 var(--card-border-radius) var(--card-border-radius);
}

@media screen and (max-width: 768px){
    .project-card {
        width: 100%;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/projectCard.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,eAAe;IACf,sBAAsB;IACtB,QAAQ;IACR,kBAAkB;IAClB,qCAAqC;IACrC,mDAAmD;IACnD,wCAAwC;IACxC,8BAA8B;IAC9B,gBAAgB;IAChB,oCAAoC;IACpC,iCAAiC;IACjC,+BAA+B;IAC/B,4BAA4B;AAChC;;AAEA;IACI,SAAS;IACT,kBAAkB;IAClB,oCAAoC;IACpC,oCAAoC;IACpC,iCAAiC;IACjC,+BAA+B;IAC/B,4BAA4B;IAC5B,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,4BAA4B;IAC5B,uCAAuC;IACvC,sEAAsE;AAC1E;;AAEA;IACI;QACI,WAAW;IACf;AACJ","sourcesContent":[".project-card {\n    display: flex;\n    flex-wrap: wrap;\n    flex-direction: column;\n    top: 0px;\n    position: relative;\n    width: calc((100% - var(--gutter))/2);\n    background-color: var(--surface-primary-background);\n    border-radius: var(--card-border-radius);\n    box-shadow: var(--card-shadow);\n    overflow: hidden;\n    -webkit-transition: all .1s ease-out;\n    -moz-transition: all .1s ease-out;\n    -o-transition: all .1s ease-out;\n    transition: all .1s ease-out;\n}\n\n.project-card:hover {\n    top: -5px;\n    position: relative;\n    box-shadow: var(--card-shadow-hover);\n    -webkit-transition: all .1s ease-out;\n    -moz-transition: all .1s ease-out;\n    -o-transition: all .1s ease-out;\n    transition: all .1s ease-out;\n    cursor: pointer;\n}\n\n.project-card > .image-wrapper{\n    /*delete later*/\n    background-color: #ddd;\n    height: 300px;\n}\n\n.project-card-body {\n    display: flex;\n    flex-direction: column;\n    gap: var(--spacing-20);\n    padding: var(--card-padding);\n    border: solid 1px var(--border-divider);\n    border-radius: 0 0 var(--card-border-radius) var(--card-border-radius);\n}\n\n@media screen and (max-width: 768px){\n    .project-card {\n        width: 100%;\n    }\n}"],"sourceRoot":""}]);
